import axios from 'axios';
import React , { useEffect, useState } from 'react';
import { useHistory , useParams } from 'react-router-dom';
// 라우터의 useParams 훅
import styled from 'styled-components';
import './Detail.scss';




//Hook 의 종류를 살펴보자
//class Detail2 extends React.Component{

    //Detail2 컴포넌트가 Mount 되었을때 실행할 코드
    // Ajax 같은것도 이곳에 자주 사용한다.
    //componentDidMount() {

   // }

    //Detail2 컴포넌트가 Unmount 되기 직전에 실행할 코드
   // componentWillUnmount() {

   // }
//}

// ==> 근데 이것말고 좀더 쉬운 문법이 개발되었다.
//     바로 useEffect 이다.


let box = styled.div`

    padding : 20px;
    
`;

let 제목 = styled.div`

    font-size : 25px;

`;


        function Detail( props ){



            // alert 5초뒤에 보이게끔 하기위해 구문 작성
            const [alert , alertChange] = useState(true);

            //임시 state 만들어보기(재렌더링 해보기 위해서)
            const [inputData , setInputData] = useState('');

            // useEffect
            //라이프사이클과 똑같은 기능을 합니다.
            //컴포넌트가 딱 보일때 실행이 되고 , 업데이트 될때도
            //실행이 된다.


            // Detail 컴포넌트가 등장 & 업데이트시 실행할 코드 useEffect
            // * useEffect가 여러개 있을경우 윗순서부터 실행이 된다.
            // 첫번쨰로실행
            useEffect( () => {

                axios.get().then().catch();

                //Detail 페이지 방문 후 alert 창이 2초후에 사라지게 해보자.
               let timer =  setTimeout( () => {
                    
                    //여기에 alert 창 안보이게끔 하는 구문을 작성
                    alertChange(false)

                    //<Detail> 이 사라질때 실행되는 코드
                    //버그같은걸 염두해두고 쓰는 코드
                    return () => {
                        clearTimeout(timer)
                    }

                } , 3000 );


            } , [alert]);
                //  [] : useEffect가 실행될 조건을 넣을 수가 있다.
                //  useEffect는 뭔가를 업데이트 할때마다 계속 재렌더링이 실행이 된다..
                //  그렇게 되면 자원낭비가 심하기때문에 이쁘게 써야한다.
                //alert 라는 조건을 넣게 되면
                //alert라는 state 가 변경이 될때만 재렌더링이 된다.
                // [] 빈배열로 둘시에 한번만 실행되고 더이상 실행되지 않는다.




            //두번쨰로 실행
            useEffect ( () => {

                //컴포넌트가 사라질떄 코드를 실행시킬수도 있다.
                return () => {
                    //이 구간이 사라질때 코드를 실행한다.

                }
            } , []);


 


            //방문기록등을 저장해놓는 오브젝트 useHistory
            let history = useHistory();

            // 라우터의 useParams 훅
            let { id } = useParams();

            // find() 라는 신문법
            // Array 안에서 원하는 자료를 찾고 싶을때 사용
            // 
            let finditem = props.shoes.find(function (item){
                
                // 조건식 참인것만 변수에 데이터 저장해줌
                return item.id == id

            });



            
            return (
        
            <div className="container">
                
             <제목 className="red">
                        Detail
             </제목>
                
                {/* useEffect -> 재렌더링이 계속 진행되는 상황 */}
                {inputData}
                <input onChange={ (e) => { setInputData(e.target.value) } }/>

                {/* useState alert 삼항연산자 활용 */}
                {
                    alert === true 
                    ?(<div className="my-alert">
                        <p>재고가 얼마 남지 않았습니다.</p>
                     </div>)
                    : null

                }
             

                <div className="row">
                    <div className="col-md-6">
                        <img src={finditem.image} width="100%"></img>
                    </div>
                    <div className="col-md-6 mt-4">
                        <h4 className="pt-5">{finditem.title}</h4>
                        <p>{finditem.content}</p>
                        <p>{finditem.price}원</p>
                        {/* 재고데이터 */}
                        <Info 재고={props.재고} 재고변경={props.재고변경}></Info>
                        <br/>
                        <button className="btn btn-danger" onClick={ () => { props.재고변경(props.재고-1) }}>주문하기</button> {/* goBack() 함수 : 뒤로가기 push(); :이쪽으로 보내주세요 */}                                                                                
                        <button className="btn btn-info"   onClick={ () => { history.push("/") }}>뒤로가기</button> 
                    </div>
                </div>
            </div> 
        
            )
        }

        function Info(props){

            function 재고감소(){

                props.재고변경(props.재고-1)     


            }

            function 재고증가(){

                props.재고변경(props.재고+1)     


            }


            return (
                <>
                <p>재고 : {props.재고}</p>
                <div>
                    <button className="btn btn-success" onClick={재고증가}>+</button>&nbsp;
                    <button className="btn btn-primary" onClick={재고감소}>-</button>
                </div>
                </>
            );

        }



        export default Detail;
